@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
  font-family: "Rubik", sans-serif;
}
.top,
.bottom {
  width: 25vw;
  height: 80vh;
  position: absolute;
  border: none;
}
.top-circle,
.bottom-circle {
  border-radius: 50%;
  width: 25vw;
  position: absolute;
  height: 50vh;
}
.top-circle {
  bottom: -10%;
  background: #ae3cff;
}
.top {
  border-bottom-left-radius: 20%;
  border-bottom-right-radius: 20%;
  background: linear-gradient(#e942ff, #8838ff);
}
.bottom {
  right: 0;
  bottom: 0;
  background: #f5f3f7;
  border-top-left-radius: 20%;
  border-top-right-radius: 20%;
  z-index: -1;
}
.bottom-circle {
  background: #f5f3f7;
  top: -10%;
}
.center {
  display: flex;
  width: 80%;
  position: absolute;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  justify-content: space-evenly;
}
.mobile-ui {
  width: 280px;
  height: 540px;
  background: #fff;
  padding: 10px;
  box-shadow: 0px 20px 15px 15px rgb(0 0 0 / 10%);
  border-radius: 50px;
}
.text {
  width: 400px;
}
h1 {
  font-size: 2.2rem;
  font-weight: 500;
  margin-bottom: 20px;
  color: hsl(271, 36%, 24%);
}
p {
  color: hsl(270, 7%, 64%);
  line-height: 25px;
  font-size: 1rem;
}
.nouch {
  background: linear-gradient(90deg, #8838ff, #e942ff);
  border-radius: 5px;
  border-top-left-radius: 40px;
  height: 90px;
  border-top-right-radius: 40px;
  position: relative;
}
.nouch::before {
  content: "";
  width: 50%;
  height: 25px;
  background: #fff;
  position: absolute;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  left: 25%;
}
.middleNouch {
  display: flex;
  align-items: center;
  position: relative;
  bottom: -40px;
  color: white;
}
.left_side {
  display: flex;
  align-items: center;
  width: 90%;
  color: hsl(0, 0%, 100%);
}
.left_side img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid hsl(0, 0%, 100%);
  margin: 0 10px;
}
span {
  font-size: 0.75rem;
  color: hsl(0deg 0% 71%);
  font-weight: 500;
}
.chats {
  height: calc(100% - 90px);
  background: #f5f3f7;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
  padding: 10px;
}
.chats p,
.price {
  width: 60%;
  line-height: normal;
  font-size: 0.7rem;
  padding: 5px 10px;
  border-radius: 12px;
  margin-bottom: 10px;
  color: #000;
}
.topLeftChat p,
.leftBottomChat p {
  background: #ede5f2;
  border-bottom-left-radius: 3px;
}
.rightMiddleChat p {
  background-color: #fff;
  border-bottom-right-radius: 3px;
}

.rightMiddleChat {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.images {
  display: flex;
}
.images img {
  width: 40px;
  height: 40px;
  object-fit: cover;
  object-position: center;
  margin: 0px 5px 10px;
  border-radius: 10px;
}
.price {
  width: 70%;
  background: linear-gradient(90deg, #e942ff, #8838ff);
  border-bottom-left-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #f5f3f7;
}
kbd {
  font-size: 0.7rem;
  color: hsl(276, 100%, 81%);
}
h6 {
  font-size: 1rem;
}
.radioButton {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid hsl(289, 100%, 72%);
}
.message {
  position: relative;
  background: #fff;
  border-radius: 20px;
}
input {
  height: 35px;
  border-radius: 20px;
  border: none;
  outline: none;
  padding: 0 20px;
}
button {
  position: absolute;
  top: 5px;
  right: 8px;
  background: black;
  color: white;
  border: none;
  font-size: 1.2rem;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 900px) {
  .top,
  .bottom,
  .top-circle,
  .bottom-circle {
    width: 35vw;
  }
  .center {
    width: 100%;
    justify-content: space-between;
  }
  .mobile-ui {
    position: relative;
    left: 11%;
  }
  .text {
    width: 350px;
  }
  p {
    font-size: 0.8rem;
  }
  h1 {
    font-size: 1.8rem;
  }
}
@media screen and (max-width: 750px) {
  .center {
    flex-direction: column;
  }
}
